import React, { CSSProperties } from "react";
import classnames from "classnames";
import { Link } from "gatsby";
import styles from "./layout.module.scss";
import MenuCategories from "./MenuCategories";
import { NewsletterBox } from "./NewsletterBox";

interface LayoutProps {
  className?: string;
  styles?: CSSProperties;
}

const Layout: React.FC<LayoutProps> = props => {
  const { children, className, styles: htmlStyles = {} } = props;

  return (
    <div>
      <div
        className={classnames(styles.root_panel, className)}
        style={htmlStyles}
      >
        <div className={styles.grid_menu}>
          <div className={classnames(styles.menu, styles.left_menu)}>
            <Link
              to="/"
              className={styles.menu_item}
              activeClassName={styles.active}
            >
              About
            </Link>
            <Link
              to="/blog"
              className={styles.menu_item}
              activeClassName={styles.active}
            >
              Blog
            </Link>
            <Link
              to="/calculators"
              className={styles.menu_item}
              activeClassName={styles.active}
            >
              Tools
            </Link>
          </div>
          <div className={classnames(styles.menu, styles.right_menu)}>
            <MenuCategories />
            <a href="/cv.pdf" className={styles.menu_item} target="_blank">
              CV
            </a>
          </div>
        </div>
        {children}
      </div>
      <footer>
        <NewsletterBox />
      </footer>
    </div>
  );
};

export default Layout;
